<template>
<div @click="click" style="position: relative">
    <v-icon 
      @mouseover="mouseOver" 
      @mouseout="mouseOut" 
      :class="{'dim': hovered && !favorite}"
      color="#f44336" >{{active ? "favorite" : "favorite_border"}}</v-icon>
</div>
    
</template>

<script>
  import store from "@/store";

  import { POST_FAVORITE } from "../vuex/actions";

  export default {
    data() {
      return {
        hovered: false
      };
    },
    props: {
      favorite: Boolean,
      id: String,
    },
    computed: {
      active() {
        return this.favorite || this.hovered;
      }
    },
    methods: {
      mouseOver() {
        this.hovered = !this.hovered;
      },
      mouseOut() {
        this.hovered = !this.hovered;
      },
      click() {
        store.dispatch(POST_FAVORITE, { id: this.id });
      }
    }
  };
</script>

<style lang="sass" scoped>
    .v-icon
      font-size: 40px
      font-weight: bold
      cursor: pointer
      margin: 5px
      user-select: none
    .dim
      opacity: 0.3
</style>
