<template>
    <div class="fada">
        <div class="details-container">
            <div class="action-bar">
                <div class="title-wrapper">
                    <heart :id="ann.id" :favorite="ann.isFavorite"/>
                    <span class="ann-title">{{ann.title}}</span>
                </div>
                <v-icon clas="back-btn" color="#1c1c1c" @click="goBack">keyboard_backspace</v-icon>
            </div>
             <div class="info-bar">
                 <span>{{ann.address}}</span>
                <div>
                    <span class="extra">{{ann.rooms}} rooms</span>
                    <span class="basic">for</span>
                    <span class="extra">{{ann.price}} zł</span>
                </div>
            </div>
            <div class="gallery">
                <div class="main-display">
                    <img class="main-photo" :src="ann.imgUrl" />
                    <div class="nav-btn-left">
                        <v-icon color="#fff" >keyboard_arrow_left</v-icon>
                    </div>
                    <div class="nav-btn-right">
                        <v-icon color="#fff" >keyboard_arrow_right</v-icon>
                    </div>
                </div>
                <div class="images-list">
                    <img class="thumbnail" :src="ann.imgUrl" v-for="n in 4" :key="n"/>
                </div>
            </div>
            <div class="data-container">
                <div>BRZYDKIE DO POPRAWY</div>
                <div class="single-data">
                    <span>Price :</span><span>{{ann.price}} zł</span>
                </div>
                <div class="single-data">
                    <span>Rooms :</span><span>{{ann.rooms}}</span>
                </div>
                <div class="single-data">
                    <span>Area :</span><span>{{ann.area}} m<sup>2</sup></span>
                </div>
                <div class="single-data">
                    <span>Address :</span><span>{{ann.address}}</span>
                </div>
            </div>
            <div class="description-container">
                <span class="description">Description : </span>
                <div class="text">
                    {{ann.description}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import store from "@/store";
    import { mapState } from "vuex";
    import Heart from "../../components/Heart.vue";
    import { STOP_BOUNCE , MAKE_IT_BOUNCE} from "../../vuex/mutations";
    export default {
      name: "ann-details",
      computed: mapState({
        ann: state =>
          state.announcements.announcements.find(
            ann => ann.id === state.announcements.currentAnnouncementId
          ) || {}
      }),
      components: {
        Heart
      },
      created(){
          store.commit(MAKE_IT_BOUNCE, {id: this.ann.id})
      },
      methods: {
        goBack() {
          this.$router.push({ name: "flats" });
        }
      },
      beforeRouteUpdate(to, from, next) {
        store.commit(MAKE_IT_BOUNCE, { id: to.params.id });
        store.commit(STOP_BOUNCE, { id: from.params.id });
        next();
      },
      beforeRouteLeave(to, from, next) {
        store.commit(STOP_BOUNCE, { id: this.ann.id });
        next();
      }
    };
</script>

<style lang="sass" scoped>
    .fada
        height: 100%
        border-radius: 0
    .details-container
        display: flex
        height: 100%
        flex-direction: column
        padding: 20px
        overflow-y: scroll
        overflow-x: hidden
        color: #1c1c1c
        .action-bar
            display: flex
            justify-content: space-between
            align-items: center
            min-height: 40px
            padding: 0 20px
            .v-icon
                font-size: 40px
                font-weight: bold
                user-select: none
                cursor: pointer
            .title-wrapper
                display: flex
                justify-content: space-between
                .ann-title
                    font-size: 30px
                    font-weight: bold
        .info-bar
            display: flex
            justify-content: space-between
            align-items: center
            min-height: 40px
            font-size: 24px
            font-weight: bold
            padding: 0 40px
            .extra
                color: #fb7600
            .basic
                color: #3c3c3c
                font-size: 20px
                margin: 0 8px
        .main-display
            position: relative
            margin-top: 20px
            .main-photo
                width: 100%
                height: 100%
                max-height: 500px
            .nav-btn
                position: absolute
                top: 50%
                transform: translate(0,-50%)
                z-index: 2
                width: 60px
                height: 120px
                border-radius: 15px
                background: #fb7600
                opacity: 0.5
                transition: opacity 0.2s
                display: flex
                align-items: center
                justify-content: center
                cursor: pointer
                user-select: none
                &:hover
                    opacity: 1
                .v-icon
                    font-size: 60px
                    font-weight: bold
            .nav-btn-left
                @extend .nav-btn
                left: 10px
            .nav-btn-right
                @extend .nav-btn
                right: 10px
        .images-list
            height: 180px
            margin: auto
            padding: 10px
            overflow-y: hidden
            overflow-x: scroll
            .thumbnail
                display: inline-block
                height: 140px
                width: 200px
                border-radius: 10px
                margin: 0 10px
                opacity: 0.7
                cursor: pointer
                transition: opacity 0.3s
                &:hover
                    opacity: 1
        .description-container
            margin-top: 20px
            .description
                font-size: 24px
                font-weight: bold
            .text
                font-size: 16px
        .data-container
            margin: 0 20px
            min-height: 40px
            display: flex
            flex-wrap: wrap
            .single-data
                margin: 15px
                min-height: 40px
                border: 1px solid #1c1c1c
                border-radius: 10px
                span
                    font-size: 20px
                    font-weight: bold
                    color: #1c1c1c
                    margin: 10px
</style>
